<template>
  <ion-page>
    <div v-if="resilers == null" style="margin-top: 50%"> <!-- login -->
      <ion-row>
        <ion-col size="12">
          <ion-item>
            <ion-input type="password" :value="pass" @input="pass=$event.target.value" class="ion-text-center"/>
          </ion-item>
        </ion-col>
        <ion-col size="12">
          <ion-button expand="block" @click="login">
            login
          </ion-button>
        </ion-col>
      </ion-row>
    </div>
    <div v-else> <!-- resilers -->
      <div v-if="resiler == null">
        <ion-row>
          <ion-col>
            <ion-list v-for="(resiler1, index) in resilers" :key="index">
              <ion-item @click="resiler = resiler1">
                <ion-label>{{resiler1.n}}</ion-label>
                <ion-label>{{resiler1.p}}</ion-label>
                <ion-label>{{resiler1.b}}</ion-label>
              </ion-item>
            </ion-list>
          </ion-col>
        </ion-row>
        <ion-fab horizontal="end" vertical="bottom">
          <ion-fab-button size="small" @click="resiler = 'create'">
            <ion-icon name="add"/>
          </ion-fab-button>
        </ion-fab>
      </div>
      <div v-else> <!-- resiler -->
      <div v-if="resiler == 'create'" style="margin-top: 50%"> <!-- Add resiler -->
        <ion-row>
          <ion-col>
            <ion-item>
              <ion-input :value="name" @input="name=$event.target.value" placeholder="اﻹسم" class="ion-text-center"/>
            </ion-item>
          </ion-col>
        </ion-row>
        <ion-row>
          <ion-col>
            <ion-item>
              <ion-input :value="phone" @input="phone=$event.target.value" inputmode="numeric" maxlength="10" placeholder="0000000000" class="ion-text-center"/>
            </ion-item>
          </ion-col>
        </ion-row>
        <ion-row>
          <ion-col>
            <ion-button expand="block" @click="create">
              save
            </ion-button>
          </ion-col>
        </ion-row>
        <br>
        <ion-row>
          <ion-col class="ion-text-center">
            <ion-button @click="resiler = null">
              back
            </ion-button>
          </ion-col>
        </ion-row>
      </div>
      <div v-else style="margin-top: 50%">
        <ion-row>
          <ion-col class="ion-text-center">
            <div>{{resiler.n}}</div>
            <div>{{resiler.p}}</div>
            <div>{{resiler.b}}</div>
          </ion-col>
        </ion-row>
        <ion-row class="ion-justify-content-center">
          <ion-col size="auto">
            <ion-button @click="addBalance(50)">
              +50
            </ion-button>
          </ion-col>
          <ion-col size="auto">
            <ion-button @click="addBalance(500)">
              +500
            </ion-button>
          </ion-col>
          <ion-col size="auto">
            <ion-button @click="addBalance(1000)">
              +1000
            </ion-button>
          </ion-col>
        </ion-row>
        <ion-row>
          <ion-col class="ion-text-center">
            <ion-button @click="resiler = null">
              back
            </ion-button>
          </ion-col>
        </ion-row>
        <ion-fab horizontal="start" vertical="bottom">
          <ion-fab-button size="small" color="danger" @click="remove">
            <ion-icon name="trash"/>
          </ion-fab-button>
        </ion-fab>
      </div>
      </div>
    </div>
  </ion-page>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      resilers: null,
      pass: null,
      resiler: null,
      name: null,
      phone: null
    }
  },
  methods: {
    async login() {
      const response = await fetch(`${this.server}/admin/resilers/login/${this.pass}`, {
        method: 'POST'
      })
      const status = response.status
      if (status == 200) {
          const resilers = await response.json()
          this.resilers = resilers.reverse()
      } else {
          console.log(response.statusText)
      }
    },
    async addBalance(b) {
      const response = await fetch(`${this.server}/admin/resilers/balance/${this.pass}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          p: this.resiler.p,
          b
        })
      })
      const status = response.status
      if (status == 200) {
          const b1 = await response.json()
          this.resiler.b = b1
          this.resilers.forEach(resiler1 => {
            if (resiler1.p == this.resiler.p) {
              resiler1.b = b1
            }
          })
      } else {
          console.log(response.statusText)
      }
    },
    async create() {
      const response = await fetch(`${this.server}/admin/resilers/${this.pass}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          n: this.name,
          p: this.phone
        })
      })
      const status = response.status
      if (status == 200) {
          this.resilers.unshift({n: this.name, p: this.phone, b: 0})
          this.resiler = null
      } else {
          console.log(response.statusText)
      }
    },
    async remove() {
      if (confirm('حذف ؟')) {
        const response = await fetch(`${this.server}/admin/resilers/${this.resiler.p}/${this.pass}`, {
          method: 'DELETE'
        })
        const status = response.status
        if (status == 200) {
          const p = this.resiler.p
            this.resilers = this.resilers.filter(resiler => resiler.p != p)
            this.resiler = null
        } else {
            console.log(response.statusText)
        }
      }
    }
  }
}
</script>
